import { DatePipe } from '@angular/common';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhQuerySales, VhAlgorithm } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { DeliveryA45Component } from 'src/app/sales/components/print/delivery/A45/delivery-a45/delivery-a45.component';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhSaleInvoices } from 'src/app/sales/interface/vh-sale-invoice';
import { LanguageService } from 'src/app/sales/services/language.service';
import { ViettelService } from '../../../services/viettelService/viettel.service';

@Component({
  selector: 'app-create-bill-customer-viettel',
  templateUrl: './create-bill-customer-viettel.component.html',
  styleUrls: ['./create-bill-customer-viettel.component.scss']
})
export class CreateBillCustomerViettelComponent implements OnInit {

  dataRestore: any = this.router.getCurrentNavigation().extras.state
  billDetails: Array<any> = []
  addInformation: FormGroup
  deliveryInfo: FormGroup
  productInfo: FormGroup
  data_delivery: any
  serviceSelected: any = {}
  data_delivery_recei: any = {}
  MONEY_COLLECTION: any
  PRODUCT_PRICE: any
  invoice: VhSaleInvoices
  listProvinceViettel: Array<any> = []
  listDistrictViettel: Array<any> = []
  listWardViettel: Array<any> = []
  listServiceViettel: Array<any> = []
  listServiceAddViettel: Array<any> = []
  isLoading: boolean = false
  dataAddress: any = { SENDER_FULLNAME: '', SENDER_ADDRESS: '', SENDER_PHONE: '', SENDER_WARD: '', SENDER_DISTRICT: '', SENDER_PROVINCE: '', WARDS_NAME: '', DISTRICT_NAME: '', PROVINCE_NAME: '' }
  heightScroll: any = '100px'
  constructor(
    private router: Router,
    private vhComponent: VhComponent,
    private vhQuerySales: VhQuerySales,
    private lang: LanguageService,
    public vhAlgorithm: VhAlgorithm,
    private viettelPost: ViettelService,
    private cdRef: ChangeDetectorRef,
    private modalService: NzModalService,
    private datePipe: DatePipe
  ) {
  }

  ngOnInit() {
    this.initForm();
  }
  initForm() {
    let customer = this.vhQuerySales.getlocalCustomer(this.dataRestore.billInfo.id_customer)
    this.addInformation = new FormGroup({
      RECEIVER_FULLNAME: new FormControl(customer.name, [Validators.required, Validators.minLength(6)]),
      RECEIVER_PHONE: new FormControl(customer.phone, [Validators.required, Validators.minLength(10), Validators.maxLength(10)]),
      RECEIVER_ADDRESS: new FormControl('', Validators.required),
      RECEIVER_PROVINCE: new FormControl('', Validators.required),
      RECEIVER_DISTRICT: new FormControl('', Validators.required),
      RECEIVER_WARD: new FormControl('', Validators.required),
    })
    this.deliveryInfo = new FormGroup({
      ORDER_PAYMENT: new FormControl('2', Validators.required),
      ORDER_SERVICE: new FormControl('', Validators.required),
      ORDER_SERVICE_ADD: new FormControl(''),
      PRODUCT_PRICE: new FormControl(0),
      MONEY_COLLECTION: new FormControl(0),
      ORDER_NOTE: new FormControl(''),
    })
    this.productInfo = new FormGroup({
      PRODUCT_WEIGHT: new FormControl('', [Validators.required, Validators.min(0.0000000000000000000001), Validators.max(99999999999)]),
      PRODUCT_LENGTH: new FormControl('', [Validators.required, Validators.max(999999999)]),
      PRODUCT_WIDTH: new FormControl('', [Validators.required, Validators.max(999999999)]),
      PRODUCT_HEIGHT: new FormControl('', [Validators.required, Validators.max(999999999)]),
    })

    this.getData();
  }
  ngAfterViewInit() {
    this.vhAlgorithm.waitingStack().then(() => {
      this.MONEY_COLLECTION = this.vhAlgorithm.vhnumeral('.MONEY_COLLECTION');
      this.PRODUCT_PRICE = this.vhAlgorithm.vhnumeral('.PRODUCT_PRICE');
    });
  }

  ngAfterViewChecked() {
    if (document.querySelector('productInfo'))
      this.heightScroll = document.querySelector('productInfo').clientHeight - 156 + 'px'

    this.cdRef.detectChanges()
  }
  //ly d liu billDetail
  getData() {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQuerySales.getBill_details_byId_bill(this.dataRestore.billInfo._id),
        this.viettelPost.getListInventory(this.dataRestore.data_delivery.token),
        this.vhQuerySales.refreshLocalAddresss(),
      ]).
        then(([invoice, stores, address]: any) => {
          this.vhComponent.hideLoading(0)
          this.billDetails = invoice.map(item => {
            let name_product = this.vhQuerySales.getlocalDetailProduct(item.id_subproduct ? item.id_subproduct : item.id_product).name;
            return { ...item, name_product: name_product, }
          })
          this.invoice = new VhSaleInvoices(this.dataRestore.billInfo, this.billDetails, this.vhQuerySales);
          let listAddresssRecei: Array<any> = this.vhQuerySales.getlocalAddresss().filter(item => item.id_delivery == this.dataRestore.data_delivery._id && item.type == 7 && item.id_customer == this.dataRestore.billInfo.id_customer);

          this.data_delivery_recei = listAddresssRecei.sort((a, b) => Date.parse(b.date) - Date.parse(a.date))[0]
          this.data_delivery_recei && this.attatchDataAddressToForm(this.data_delivery_recei)

          if (!stores.error) this.addDataStore(stores.data, this.dataRestore.data_delivery.GROUPADDRESS_ID)
          let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_sales')
        if (printer) this.checkPrinter = printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable;

        }).catch(err => { console.error(err); this.vhComponent.hideLoading(0) })
    })
  }

  addDataStore(stores: Array<any>, GROUPADDRESS_ID) {
    let store = stores.find(item => item.groupaddressId == GROUPADDRESS_ID)
    if (store)
      this.dataAddress = {
        ...this.dataAddress,
        SENDER_FULLNAME: store.name, SENDER_ADDRESS: store.address, SENDER_PHONE: store.phone,
        SENDER_WARD: store.wardsId, SENDER_DISTRICT: store.districtId, SENDER_PROVINCE: store.provinceId,

      }
  }
  checkPrinter: boolean = false;
  goBack(success?) {
    this.router.navigate(["/sales/dashboard/manage/delivery/shipping-bill/delivery-viettel"], {
      state: { dataRestore: this.dataRestore }
    })
  }
  createOrder(print?) {
    
    this.modalService.confirm({
      nzTitle: this.lang.translate("Do you want to create order ?"),
      nzCancelText: this.lang.translate("Cancel"),
      nzOnOk: () => {
        return new Promise<any>((resolve) => {
          this.viettelPost.createOrderViettelPost(this.dataRestore.data_delivery.token, { ...this.addInformation.value, ...this.productInfo.value, ...this.deliveryInfo.value, ...this.dataAddress, MONEY_COLLECTION: this.MONEY_COLLECTION.getRawValue(), PRODUCT_PRICE: this.PRODUCT_PRICE.getRawValue(), ...this.dataRestore.data_delivery, DELIVERY_DATE: this.datePipe.transform(new Date(), 'dd/MM/yyyy HH:mm:ss') }, this.billDetails, this.invoice.getBillCode()).then((res: any) => {
            if (!res.error) {
              this.invoice.setOrderCode(res.data.ORDER_NUMBER)
              this.invoice.setDeliveryViettelData({ created: new Date().toISOString(), ...res.data, RECEIVER_FULLNAME: this.addInformation.value.RECEIVER_FULLNAME, RECEIVER_PHONE: this.addInformation.value.RECEIVER_PHONE, ORDER_PAYMENT: this.deliveryInfo.value.ORDER_PAYMENT })
              this.vhQuerySales.updateBill_Billdetail(this.invoice.getID(), this.invoice.getCreateUpdateInvoice(1), this.invoice.getCreateUpdateInvoiceDetail()).then((data: any) => {
                resolve(true)
                this.dataRestore.reload = true
                this.vhComponent.alertMessageDesktop('success', this.lang.translate('Create order successfully !'))
                if (print) this.showPrint(this.invoice.getDeliveryViettelData())
                else this.goBack()
              })
            } else this.vhComponent.alertMessageDesktop('error', res.message)
          }).catch(err => this.vhComponent.alertMessageDesktop('error', err.message))
        })
      },
      nzOnCancel: () => { },
    })
  }
   /**
   * ly thng tin d liu trang in
   * @returns Promise<any>
   * @example this.checkPrint()
   */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_sales')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }

  showPrint(res) {
    this.checkPrint().then((printer) => {
      this.vhComponent.showModal(this.renderPrintPage(printer['_id']), {
        billInfo: {
          ...this.dataRestore.billInfo, data: this.viettelPost.renderFormatPrintOrder({ ...this.dataAddress, ...res, ...this.addInformation.value, ...this.productInfo.value, ...this.deliveryInfo.value, MONEY_COLLECTION: this.deliveryInfo.value.MONEY_COLLECTION }), order_code: res.ORDER_NUMBER
        },
        billDetails: this.billDetails,
        data_delivery: { ...this.data_delivery, type: 5 },
        printer
      }, false, false, "modal-print").then(modal => {
        modal.onWillDismiss().then(() => {
          this.goBack(true);
        })
      })

    }, (err) => {
      this.goBack(true)
    })
  }

  /**
   * tr v component ca trang in tng ng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'K57' | 'K80' | 'A4') {
    switch (type) {
      default: return DeliveryA45Component
    }
  }
  customDataBillInfo(res) {

  }
  //------------------------------------Xa huyn tinh------------------------------------

  getAllService() {
    if (this.addInformation.valid && this.productInfo.valid) {
      this.viettelPost.getServiceViettel(this.dataRestore.data_delivery.token, { ...this.addInformation.value, ...this.productInfo.value, ...this.deliveryInfo.value, ...this.dataAddress, MONEY_COLLECTION: this.MONEY_COLLECTION.getRawValue(), PRODUCT_PRICE: this.PRODUCT_PRICE.getRawValue() }).then((result: any) => {
        if (result.length) this.listServiceViettel = result
        else this.vhComponent.alertMessageDesktop('error', result.message)
        this.getPriceOfServiceSelected()
      })
    }
  }
  changeService(event) {
    this.serviceSelected = {}
    this.listServiceAddViettel = this.listServiceViettel.find(item => item.MA_DV_CHINH == event).EXTRA_SERVICE
    this.getPriceOfServiceSelected()
  }
  getPriceSpecially() {
    let ORDER_SERVICE_ADD: string = ''
    this.listServiceAddViettel.forEach(item => {
      if (item.checked) ORDER_SERVICE_ADD += `${item.SERVICE_CODE},`
    })
    ORDER_SERVICE_ADD = ORDER_SERVICE_ADD.slice(0, ORDER_SERVICE_ADD.lastIndexOf(','))
    this.deliveryInfo.controls.ORDER_SERVICE_ADD.setValue(ORDER_SERVICE_ADD)
    this.getPriceOfServiceSelected()
  }

  getPriceOfServiceSelected() {
    if (this.addInformation.valid && this.productInfo.valid && this.deliveryInfo.value.ORDER_SERVICE) {
      this.viettelPost.getPriceSpeciallyViettel(this.dataRestore.data_delivery.token, { ...this.addInformation.value, ...this.productInfo.value, ...this.deliveryInfo.value, ...this.dataAddress, MONEY_COLLECTION: this.MONEY_COLLECTION.getRawValue(), PRODUCT_PRICE: this.PRODUCT_PRICE.getRawValue() }).then((result: any) => {
        if (result.error) this.vhComponent.alertMessageDesktop('error', result.message)
        else this.serviceSelected = result.data
        
      })
    }
  }
  /**Thay i cn nng */
  changePRODUCT_WEIGHT() {
    if (this.productInfo.value.PRODUCT_WEIGHT) this.getAllService()
  }
  //Vit hoa ch cai u cua mi t
  uppercaseFirstChar(str: string) {
    let array = str.toLowerCase().split(' ');
    let newStr = '';
    array.forEach((item) => {
      newStr += item.charAt(0).toUpperCase() + item.slice(1) + ' ';
    });
    return newStr
  }
  // ----------modal list address pickup---------
  pickupAdresssViettel: boolean = false;
  dataPickupAddressViettel: any;
  cancelListViettel(event) {
    if (event && event._id) {
      this.data_delivery_recei = event;
      this.listServiceViettel = []
      this.listServiceAddViettel = []
      this.deliveryInfo.controls.ORDER_SERVICE.setValue('')
      this.deliveryInfo.controls.ORDER_SERVICE_ADD.setValue('')
      this.attatchDataAddressToForm(event)
    }
    else if (event && !event._id) {
      this.data_delivery_recei = {}
      this.addInformation.controls.RECEIVER_WARD.setValue('')
    }
    this.pickupAdresssViettel = false;
  }
  /**
   * Ham nay  chuyn a d liu vao form nu co
   * @param receive 
   */
  attatchDataAddressToForm(receive: any) {
    this.addInformation.controls.RECEIVER_ADDRESS.setValue(receive.RECEIVER_ADDRESS)
    this.addInformation.controls.RECEIVER_PROVINCE.setValue(receive.RECEIVER_PROVINCE)
    this.addInformation.controls.RECEIVER_DISTRICT.setValue(receive.RECEIVER_DISTRICT)
    this.addInformation.controls.RECEIVER_WARD.setValue(receive.RECEIVER_WARD)
    this.dataAddress.WARDS_NAME = receive.WARDS_NAME
    this.dataAddress.DISTRICT_NAME = receive.DISTRICT_NAME
    this.dataAddress.PROVINCE_NAME = receive.PROVINCE_NAME
    
    this.getAllService()
  }
}
