import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuerySales, VhQuery, VhAuth } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
import { ViettelService } from '../../services/viettelService/viettel.service';

@Component({
  selector: 'app-delivery-viettel',
  templateUrl: './delivery-viettel.component.html',
  styleUrls: ['./delivery-viettel.component.scss']
})
export class DeliveryViettelComponent implements OnInit {

  bills: Array<any> = [];
  show_bills: any = [];
  //Hm nay 0h- 23h59
  public date: Date[] = [new Date(), new Date()];
  dataRestore: any = this.router.getCurrentNavigation().extras.state?.dataRestore
  //bin c show
  status_open: boolean = false;
  data_delivery: any;
  search: string = '';
  disabledDate = (current: Date): boolean => {
    if (current.getTime() < new Date(this.vhAuth.getStore().registrationdate).getTime() - 24 * 60 * 60 * 1000) return true
    return false
  }
  constructor(
    public lang: LanguageService,
    private router: Router,
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private viettelPost: ViettelService,
    private vhQuerySales: VhQuerySales,
    private vhQuery: VhQuery,
    private cdRef: ChangeDetectorRef,
    private vhAuth: VhAuth
  ) {
    this.date[0].setHours(0, 0, 0, 0)
    this.date[1].setHours(23, 59, 59, 59)
  }

  ngOnInit(): void {
    if (this.dataRestore) {
      this.search = this.dataRestore.dataRestore.search
      this.date = [new Date(this.dataRestore.dataRestore.date[0]), new Date(this.dataRestore.dataRestore.date[1])];
      this.bills = this.dataRestore.dataRestore.bills
      this.show_bills = [...this.dataRestore.dataRestore.bills]
      this.data_delivery = this.dataRestore.data_delivery
      if (this.dataRestore.reload) this.getInvoices();
    }
  }
  goBack() {
    this.router.navigate(["/sales/dashboard/manage/delivery/shipping-bill"]);
  }
  /**
    * Hm ly bill trong khong thi gian c set
    */
  getInvoices() {
    this.bills = []
    this.status_open = false;
    this.data_delivery = this.vhQuerySales.getlocalDeliverys().filter(item => item.active && item.type == 5)[0];
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      this.vhQuerySales.getBills_byFields( { date: { $gte: new Date(this.date[0].setHours(0, 0, 0)), $lte: new Date(this.date[1].setHours(23, 59, 59, 59)) }, bill_type: { $eq: 1 }, id_branch: { $eq: this.vhQuerySales.getDefaultBranch()._id } }, {}).then((result: Array<any>) => {
        result.forEach(item => {
          if (item.id_delivery == this.data_delivery._id) this.bills.unshift({ ...item, customer_name: this.vhQuerySales.getlocalCustomer(item.id_customer).name })
        })
        
        this.show_bills = [...this.bills]
        this.vhComponent.hideLoading(0)
      }).catch(err => { console.error(err); this.vhComponent.hideLoading(0) })
    })
  }
  changeTime(event) {
    if (event[1].getTime() - event[0].getTime() <= 2678400000) {
    } else {
      this.vhComponent.showToast(2000, this.lang.translate("The time to view no more than 30 days"), "alert-toast")
      this.date = [new Date(event[0]), new Date(event[0].getTime() + 2678313600)]
    }
  }
  //searchbill
  searchBill(value) {
    this.show_bills = this.vhAlgorithm.searchList(
      this.vhAlgorithm.changeAlias(value).toLowerCase(),
      this.bills,
      ["bill_code", "order_code"]
    );
  }
  gotoCreateOrder(bill) {
    let state = { billInfo: bill, data_delivery: this.data_delivery, dataRestore: { search: this.search, date: this.date, bills: this.bills } }
    this.router.navigate([`/sales/dashboard/manage/delivery/shipping-bill/delivery-viettel/create-order-${bill.id_customer != "id_retail" ? 'customer' : 'retail'}`], { state: state });
  }
  gotoBillDetail(bill) {
    let state = { billInfo: bill, data_delivery: this.data_delivery, dataRestore: { search: this.search, date: this.date, bills: this.bills } }
    this.router.navigate(["/sales/dashboard/manage/delivery/shipping-bill/delivery-viettel/detail-bill"], { state: state })
  }
  /**
  * filter cc trng thi ca bill 
  * */
  // filterStatus(value) {
  //   this.searchBill(this.search)
  //   if (value != 0) this.show_bills = [...this.show_bills].filter(item => item.delivery && item.delivery.OrderStatusId == this.getStatusOfOrder(value))
  //   // this.searchBill(this.search)
  // }
  onCurrentPageDataChange(event) { }
  heightScroll: string;
  ngAfterViewChecked() {
    if (document.getElementById("delivery-viettel") && document.querySelector(".delivery-viettel-header") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.getElementById("delivery-viettel").clientHeight - document.querySelector(".delivery-viettel-header").clientHeight -
        document.querySelector(".ant-table-pagination").clientHeight - 135 + "px";
    }
    this.cdRef.detectChanges();
  }

}
