import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuerySales, VhAuth } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { DeliveryA45Component } from 'src/app/sales/components/print/delivery/A45/delivery-a45/delivery-a45.component';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhSaleInvoice, VhSaleInvoiceDetail, VhSaleInvoices } from 'src/app/sales/interface/vh-sale-invoice';
import { LanguageService } from 'src/app/sales/services/language.service';
import { ViettelService } from '../../../services/viettelService/viettel.service';

@Component({
  selector: 'app-detail-bill-viettel',
  templateUrl: './detail-bill-viettel.component.html',
  styleUrls: ['./detail-bill-viettel.component.scss']
})
export class DetailBillViettelComponent implements OnInit {

  billInfo: any;
  inforCustomer: any;
  billDetail: any;
  dataSearchAndDate: any;
  dataRestore: any = this.router.getCurrentNavigation().extras.state
  dataAddress: any = {}
  loading: boolean = false
  public invoiceInfo: any;
  public invoice: VhSaleInvoices
  constructor(
    private lang: LanguageService,
    private router: Router,
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private vhAuth: VhAuth,
    private modalService: NzModalService,
    private viettelPost: ViettelService
  ) {
    this.billInfo = this.dataRestore.billInfo
  }

  ngOnInit() {
    this.getData();
  }
  getData() {
    this.vhComponent.showLoading("").then(() => {
      Promise.all([
        this.vhQuerySales.getBill(this.dataRestore.billInfo._id),
        this.vhQuerySales.getBill_details_byId_bill(this.dataRestore.billInfo._id),
        this.viettelPost.getProvinceViettel(),
        this.viettelPost.getDistrictViettel(this.billInfo.delivery_viettel_data.RECEIVER_PROVINCE),
        this.viettelPost.getWardViettel(this.billInfo.delivery_viettel_data.RECEIVER_DISTRICT),
      ]).then((res: Array<any>) => {
        this.vhComponent.hideLoading(0)
        let invoiceDetailFresh: VhSaleInvoiceDetail[] = res[1].map(item => {
          if (item.combos) {
            let combo: any = {
              ...item,
              ... this.getNameProduct(item.id_product, item.id_subproduct),
              combos: item.combos.map(element => {
                return { ...element, ... this.getNameProduct(element.id_product, element.id_subproduct) }
              })
            }

            return { ...combo, subComboName: this.getSubCombo(combo.combos) }
          } else
            return {
              ...item,
              ... this.getNameProduct(item.id_product, item.id_subproduct)
            }
        })
        this.init(res[0], invoiceDetailFresh)
        
        this.loading = true
        this.dataAddress.PROVINCE_NAME = res[2].data.find(item => item.PROVINCE_ID == this.billInfo.delivery_viettel_data.RECEIVER_PROVINCE).PROVINCE_NAME
        this.dataAddress.DISTRICT_NAME = this.uppercaseFirstChar(res[3].data.find(item => item.DISTRICT_ID == this.billInfo.delivery_viettel_data.RECEIVER_DISTRICT).DISTRICT_NAME)
        this.dataAddress.WARDS_NAME = this.uppercaseFirstChar(res[4].data.find(item => item.WARDS_ID == this.billInfo.delivery_viettel_data.RECEIVER_WARDS).WARDS_NAME)
      })
    })
  }
  getNameProduct(id_prod: string, id_subProd: string) {
    let product = this.vhQuerySales.getlocalProduct(id_prod)
    let subFood: any
    if (id_subProd) subFood = product.subs.find(item => item._id == id_subProd)
    return { name: `${product ? product.name : ''} ${subFood ? '(' + subFood.name + ')' : ''}`, unit: product.unit }
  }
  /**Render cc thnh phn combo */
  getSubCombo(arr: any[]) {
    let subName: string = ""
    arr.forEach(item => {
      if (subName.length)
        subName += `, ${item.quantity} ${item.name}`
      else subName += `${item.quantity} ${item.name}`
    })
    return subName
  }
  init(invoice: VhSaleInvoice = null, invoiceDetail: VhSaleInvoiceDetail[] = null) {
    this.invoice = new VhSaleInvoices(invoice, invoiceDetail, this.vhQuerySales)
    if (invoice && invoiceDetail) {
      this.invoiceInfo = {
        employee: this.vhAuth.getlocalEmployee(this.invoice.getIdEmployee()),
        partner: this.vhQuerySales.getlocalCustomer(this.invoice.getIdPartner()),
      }

      if (this.invoice.getIdDelivery()) this.invoiceInfo['delivery'] = (this.vhQuerySales.getlocalDelivery(this.invoice.getIdDelivery()).name)
    } else this.invoiceInfo = { employee: {}, partner: {}, paymentName: '' }
  }
  cancelOrder() {
    if (this.vhAuth.checkMyPermission("shipping_enable_cancel_bill")) {
      this.modalService.confirm({
        nzTitle: this.lang.translate("Do you want to delete shipping bill ?"),
        nzCancelText: this.lang.translate("Cancel"),
        nzOnOk: () => {
          return new Promise<any>((resolve) => {
            let json = this.billInfo.delivery_viettel_data
            json.deleted = true
            this.invoice.setDeliveryViettelData(json)
            this.viettelPost.cancelOrderViettel(this.dataRestore.data_delivery.token, this.billInfo.delivery_viettel_data?.ORDER_NUMBER).then(res => {
              this.vhQuerySales.updateBill_Billdetail(this.invoice.getID(), this.invoice.getCreateUpdateInvoice(1), this.invoice.getCreateUpdateInvoiceDetail()).then(res => {
                this.vhComponent.alertMessageDesktop('success', this.lang.translate('Delete bill successfully'))
                this.dataRestore.reload = true
                this.goBack()
                resolve(true)
              })
            }).catch(err => this.vhComponent.alertMessageDesktop('error', err))
          })
        },
        nzOnCancel: () => { },
      })
    }
    else this.vhComponent.showToast(1500, this.lang.translate('You do not have this rights'), "alert-toast")
  }
  goBack() {
    this.router.navigate(["/sales/dashboard/manage/delivery/shipping-bill/delivery-viettel"], {
      state: { dataRestore: this.dataRestore }
    })
  }
  /*----------Print------------------*/
   /**
   * ly thng tin d liu trang in
   * @returns Promise<any>
   * @example this.checkPrint()
   */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_sales')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }

   /** kim tra d liu my in v show component in tng ng
   * @example this.showPrint() 
   */
  showPrint() {
    this.checkPrint().then((printer) => {
      this.vhComponent.showModal(this.renderPrintPage(printer['_id']), {
        billInfo: {
          ...this.dataRestore.billInfo, data: this.viettelPost.renderFormatPrintOrder({ ...this.billInfo.delivery_viettel_data, ...this.dataAddress, MONEY_COLLECTION: this.billInfo.delivery_viettel_data.MONEY_COLLECTION.toString() }), order_code: this.billInfo.delivery_viettel_data?.ORDER_NUMBER
        },
        billDetails: this.invoice.getInvoiceDetail(),
        printer,
        data_delivery: { ...this.dataRestore.data_delivery, type: 5 }
      }, false, false, `modal-print-${printer['type']}`)
    }, (err) => {
      let name :any= 'Sales'
      this.vhComponent.alertMessage(this.lang.translate("Function"), "", this.lang.translate("Please turn on the printer function"), "OK")
        .then(() => this.router.navigate(['/sales/dashboard/manage/printer','page_desktop_sales'],{state : name}))
    })
  }

  /**
   * tr v component ca trang in tng ng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'K57' | 'K80' | 'A4') {
    switch (type) {
      default: return DeliveryA45Component
    }
  }
  //Vit hoa ch cai u cua mi t
  uppercaseFirstChar(str: string) {
    let array = str.toLowerCase().split(' ');
    let newStr = '';
    array.forEach((item) => {
      newStr += item.charAt(0).toUpperCase() + item.slice(1) + ' ';
    });
    return newStr
  }
}
