import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DeliveryViettelComponent } from './delivery-viettel.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { ViettelModule } from '../../carrier-partner/components/viettel-component/viettel.module';
import { CreateBillCustomerViettelComponent } from './create-bill-customer-viettel/create-bill-customer-viettel.component';
import { CreateBillRetailViettelComponent } from './create-bill-retail-viettel/create-bill-retail-viettel.component';
import { DetailBillViettelComponent } from './detail-bill-viettel/detail-bill-viettel.component';
const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: "",
        component: DeliveryViettelComponent
      },
      {
        path: "create-order-retail",
        component: CreateBillRetailViettelComponent
      },
      {
        path: "create-order-customer",
        component: CreateBillCustomerViettelComponent
      },
      {
        path: "detail-bill",
        component: DetailBillViettelComponent
      }
    ]
  }

]

@NgModule({
  declarations: [DeliveryViettelComponent, CreateBillCustomerViettelComponent, CreateBillRetailViettelComponent, DetailBillViettelComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    FormsModule,
    TranslateModule,
    ReactiveFormsModule,
    ViettelModule
  ]
})
export class DeliveryViettelModule { }
